import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { getIEVersion } from './utils/browser-utils';
import baseStyles from './node-renderer-default.scss';
import { isDescendant, getDescendantCount } from './utils/tree-data-utils';

import './node-renderer-custom.css';


let styles = baseStyles;
// Add extra classes in browsers that don't support flex
if (getIEVersion < 10) {
  styles = {
    ...baseStyles,
    row: `${styles.row} ${styles.row_NoFlex}`,
    rowContents: `${styles.rowContents} ${styles.rowContents_NoFlex}`,
    rowLabel: `${styles.rowLabel} ${styles.rowLabel_NoFlex}`,
    rowToolbar: `${styles.rowToolbar} ${styles.rowToolbar_NoFlex}`,
  };
}

function createClassReg(className) {
  return new RegExp('\\b' + className + '\\b');
}
function inContainer(className, node) {
  var {documentElement} = document;

  while (node && (node != documentElement)) {
    if (createClassReg(className).test(node.className)) {
      return true;
    }

    node = node.parentNode;
  }

  return false;
}


class NodeRendererCustom extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isEdit: false,
      width: '',
      value: ''
    }

    this.saveInputVal = this.saveInputVal.bind(this);
    this.inputHandler = this.inputHandler.bind(this);
    this.keyupHandler = this.keyupHandler.bind(this);
    this.dblClickHandler = this.dblClickHandler.bind(this);
  }

  saveInputVal() {
    var {node, path, treeIndex, onInputSave} = this.props;
    var {value} = this.state;

    onInputSave({node, path, treeIndex, value});
    this.setState({isEdit: false});
  }

  inputHandler(e) {
    this.setState({value: e.target.value});
  }

  keyupHandler(e) {
    if (e.keyCode == 13) {
      this.saveInputVal();
    }
  }

  dblClickHandler(e) {
    var {node} = this.props;
    var content = this.content,
        container, width, containerStyle, contentStyle;

    if (e.target.nodeName == 'INPUT' || inContainer(styles.rowToolbar, e.target)) {
      return;
    }

    if (content) {
      container = content.parentNode;
      contentStyle = getComputedStyle(content);
      containerStyle = getComputedStyle(container);
      width = container.clientWidth - parseInt(containerStyle.paddingLeft) - parseInt(containerStyle.paddingRight) - parseInt(contentStyle.paddingLeft) - parseInt(contentStyle.paddingRight);
    } else {
      width = '';
    }

    this.setState({
      isEdit: true,
      value: node.title,
      width
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.isEdit && !prevState.isEdit) {
      this.input && this.input.focus();
    }
  }

  render() {
    const {
      scaffoldBlockPxWidth,
      toggleChildrenVisibility,
      onInputSave,
      connectDragPreview,
      connectDragSource,
      isDragging,
      canDrop,
      canDrag,
      node,
      draggedNode,
      path,
      treeIndex,
      isSearchMatch,
      isSearchFocus,
      buttons,
      className,
      style,
      didDrop,
      /* eslint-disable no-unused-vars */
      isOver: _isOver, // Not needed, but preserved for other renderers
      parentNode: _parentNode, // Needed for drag-and-drop utils
      endDrag: _endDrag, // Needed for drag-and-drop utils
      startDrag: _startDrag, // Needed for drag-and-drop utils
      /* eslint-enable no-unused-vars */
      ...otherProps
    } = this.props;

    var {isEdit, value, width} = this.state;

    let handle;
    if (canDrag) {
      if (typeof node.children === 'function' && node.expanded) {
        // Show a loading symbol on the handle when the children are expanded
        //  and yet still defined by a function (a callback to fetch the children)
        handle = (
          <div className={styles.loadingHandle}>
            <div className={styles.loadingCircle}>
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
              <div className={styles.loadingCirclePoint} />
            </div>
          </div>
        );
      } else {
        // Show the handle used to initiate a drag-and-drop
        handle = connectDragSource(<div className={styles.moveHandle} />, {
          dropEffect: 'copy',
        });
      }
    }

    function _connectDragSource(component) {
      return canDrag ? connectDragSource(component) : component
    }

    const isDraggedDescendant = draggedNode && isDescendant(draggedNode, node);
    const isLandingPadActive = !didDrop && isDragging;

    return (
      <div style={{ height: '100%' }} {...otherProps}>
        {toggleChildrenVisibility &&
          node.children &&
          node.children.length > 0 &&
          <div>
            <button
              type="button"
              aria-label={node.expanded ? 'Collapse' : 'Expand'}
              className={
                node.expanded ? styles.collapseButton : styles.expandButton
              }
              style={{ left: -0.5 * scaffoldBlockPxWidth }}
              onClick={() =>
                toggleChildrenVisibility({
                  node,
                  path,
                  treeIndex,
                })}
            />

            {node.expanded &&
              !isDragging &&
              <div
                style={{ width: scaffoldBlockPxWidth }}
                className={styles.lineChildren}
              />}
          </div>}

        <div className={styles.rowWrapper}>
          {/* Set the row preview to be used during drag and drop */}
          {connectDragPreview(
            <div
              className={
                styles.row +
                (isLandingPadActive ? ` ${styles.rowLandingPad}` : '') +
                (isLandingPadActive && !canDrop
                  ? ` ${styles.rowCancelPad}`
                  : '') +
                (isSearchMatch ? ` ${styles.rowSearchMatch}` : '') +
                (isSearchFocus ? ` ${styles.rowSearchFocus}` : '') +
                (className ? ` ${className}` : '')
              }
              style={{
                opacity: isDraggedDescendant ? 0.5 : 1,
                ...style,
              }}
            >
              {_connectDragSource(
                <div
                  className={
                    styles.rowContents + ' cyc-rst__rowContents' +
                    (!canDrag ? ` ${styles.rowContentsDragDisabled}` : '')
                  }
                  onDoubleClick={this.dblClickHandler}
                >
                  <div className={styles.rowLabel} ref={o => this.content = o}>
                    {
                      isEdit ? 
                      <input className="cyc-rst__rowInput" value={value} ref={o => this.input = o} onBlur={this.saveInputVal} onKeyUp={this.keyupHandler} onChange={this.inputHandler} style={{width}} /> :
                      <span className={styles.rowTitle}>
                        {typeof node.renderTitle === 'function'
                          ? node.renderTitle({
                              node,
                              path,
                              treeIndex,
                            })
                          : node.title}
                      </span>
                    }
                  </div>

                  <div className={styles.rowToolbar + ' cyc-rst__rowToolbar'}>
                    {buttons.map((btn, index) =>
                      <div
                        key={index} // eslint-disable-line react/no-array-index-key
                        className={styles.toolbarButton}
                      >
                        {btn}
                      </div>
                    )}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    );
  }
}

NodeRendererCustom.defaultProps = {
  isSearchMatch: false,
  isSearchFocus: false,
  canDrag: false,
  toggleChildrenVisibility: null,
  // onInputSave: null,
  buttons: [],
  className: '',
  style: {},
  parentNode: null,
  draggedNode: null,
  canDrop: false,
};

NodeRendererCustom.propTypes = {
  node: PropTypes.shape({}).isRequired,
  path: PropTypes.arrayOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number])
  ).isRequired,
  treeIndex: PropTypes.number.isRequired,
  isSearchMatch: PropTypes.bool,
  isSearchFocus: PropTypes.bool,
  canDrag: PropTypes.bool,
  scaffoldBlockPxWidth: PropTypes.number.isRequired,
  toggleChildrenVisibility: PropTypes.func,
  // onInputSave: PropTypes.func,
  buttons: PropTypes.arrayOf(PropTypes.node),
  className: PropTypes.string,
  style: PropTypes.shape({}),

  // Drag and drop API functions
  // Drag source
  connectDragPreview: PropTypes.func.isRequired,
  connectDragSource: PropTypes.func.isRequired,
  parentNode: PropTypes.shape({}), // Needed for drag-and-drop utils
  startDrag: PropTypes.func.isRequired, // Needed for drag-and-drop utils
  endDrag: PropTypes.func.isRequired, // Needed for drag-and-drop utils
  isDragging: PropTypes.bool.isRequired,
  didDrop: PropTypes.bool.isRequired,
  draggedNode: PropTypes.shape({}),
  // Drop target
  isOver: PropTypes.bool.isRequired,
  canDrop: PropTypes.bool,
};

export default NodeRendererCustom;



